<template>
  <div class="issueInfos">
    <PublicHeader>发布动态</PublicHeader>
    <div class="content" v-if="login">
      <h2 class="box">标题</h2>
      <input type="text" placeholder="请输入标题 （12字以内）" class="box" v-model="title">
      <h2 class="box">内容</h2>
      <textarea placeholder="请输入内容" class="box" v-model="text"></textarea>
      <div class="file box">
        <div class="fileBox">
          <input type="file" @change="getFile" id='file' multiple>
          <img :src="src" alt="">
        </div>
      </div>
      <div class="city box">
        <i class="iconfont">&#xe633;</i>
        <span>显示城市</span>
      </div>
      <div class="issue" @click="issue"><span>发布</span></div>
    </div>
    <NoLogin v-else></NoLogin>
  </div>
</template>
<script>
import PublicHeader from 'components/PublicHeader'
import NoLogin from 'components/NoLogin'
import { issueInfos } from 'api/api'
export default {
  components: { PublicHeader, NoLogin },
  data () {
    return {
      title: '',
      text: '',
      img: '',
      login: true,
      src: this.baseUrl + '/public/img/file.png'
    }
  },
  methods: {
    getFile (e) {
      this.imgs = e.target.files
    },
    issue () {
      let userInfo = localStorage.getItem('userInfo')
      if (!userInfo) {
        alert('登录后才能发动态哦')
        return false
      }
      userInfo = JSON.parse(userInfo)
      if (!userInfo.state) {
        alert('登录后才能发动态哦')
        return false
      }
      const data = new FormData()
      for (const i in this.imgs) {
        data.append('img', this.imgs[i])
      }
      data.append('title', this.title)
      data.append('text', this.text)
      data.append('userId', userInfo.userId)
      data.append('userName', userInfo.userName)
      data.append('avator', userInfo.avator)
      issueInfos(data)
        .then((res) => {
          alert('发布成功')
        })
    },
    mounted () {
      let userInfo = localStorage.getItem('userInfo')
      if (!userInfo) { this.login = false; return false }
      userInfo = JSON.parse(userInfo)
      if (!userInfo.state) { this.login = false; console.log(userInfo.state); return false }
      if (userInfo.state) {
        this.login = true
      }
    }
  }
}
</script>
<style lang="less" scoped>
.issueInfos{
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 1;
  background: #fff;
  background: #f8f8f8;
  .content{
    position: absolute;
    top: .88rem;
    left: 0;
    right: 0;
    border-top: 1px solid #dededf;
    .box{
      padding: 0 .3rem;
      width: 100%;
    }
    h2{
      font-size: .26rem;
      line-height: 0.84rem;
      color: #6a6a6a;
    }
    input.box{
      height: 1rem;
      line-height: 1rem;
      font-size: .28rem;
    }
    textarea.box{
      height: 3.4rem;
      font-size: .28rem;
      line-height: .42rem;
      padding: 0.29rem .3rem 0;
      box-sizing: border-box;
    }
    .file{
      margin-top: .63rem;;
      .fileBox{
        width: 1.7rem;
        height: 1.7rem;
        background: lightblue;
        position: relative;
        input{
          width: 100%;
          height: 100%;
          opacity: 0;
          position: absolute;
          left: 0;
          top: 0;
          z-index: 1;
        }
        img{
          width: 100%;
          height: 100%;
          position: absolute;
          left: 0;
          top: 0;
        }
      }
    }
    .city{
      display: flex;
      align-items: center;
      color: #b5b5b5;
      margin-top: .45rem;
      font-size: .34rem;
      line-height: .4rem;
      .iconfont{
        margin-right: .06rem;
        font-size: .50rem;
        line-height: .5rem;
      }
    }
    .issue{
      width: 6.4rem;
      height: 0.9rem;
      line-height: 0.9rem;
      border-radius: .9rem;
      background: #e0e0e0;
      font-size: .28rem;
      text-align: center;
      color: #888888;
      margin: 1rem auto 0;
    }
  }
}
</style>
